:root {
  --notification-color: var(--secondary-info-color);
  --notification-background: var(--primary-info-color); }

.Notification {
  --animation-duration: 0;
  /* to sync CSS animation and JS callback */
  will-change: max-height, opacity, transform, padding, margin;
  overflow: hidden;
  max-width: 100vw;
  position: relative;
  padding: 12px 1.2rem;
  margin-bottom: 5px;
  top: 0;
  color: var(--notification-color);
  background: var(--notification-background); }
  @media (max-width: 810px) {
    .Notification {
      padding: 12px 1.4rem; } }
  @media (min-width: 811px) {
    .Notification {
      display: inline-block;
      max-width: 550px;
      margin-bottom: 20px; } }
  .Notification-Text {
    margin-top: 0;
    font-weight: bold;
    margin-right: 40px; }
  .Notification-Debug {
    white-space: pre-wrap;
    background: white;
    color: black;
    padding: 5px;
    max-height: 150px;
    overflow: scroll; }
  .Notification-Button {
    cursor: pointer;
    font-size: 0;
    appearance: none;
    background: none;
    position: absolute;
    right: 0;
    top: 0;
    padding: 10px;
    width: 35px;
    height: 35px;
    opacity: 1; }
    @media (max-width: 810px) {
      .Notification-Button {
        margin: auto;
        bottom: 0;
        right: 1.4rem; } }
    .Notification-Button:focus {
      outline: none; }
    .Notification-Button::before, .Notification-Button::after {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      margin: auto;
      content: ' ';
      height: 15px;
      width: 2px;
      background-color: var(--notification-color); }
    .Notification-Button::before {
      transform: rotate(45deg); }
    .Notification-Button::after {
      transform: rotate(-45deg); }
  .Notification_is_opening {
    animation: fadeInLeft var(--animation-duration) cubic-bezier(0.645, 0.045, 0.355, 1) forwards; }
  .Notification_is_closing {
    animation: fadeOutLeft var(--animation-duration) cubic-bezier(0.645, 0.045, 0.355, 1) forwards; }
  .Notification_type_error {
    --notification-color: var(--secondary-error-color);
    --notification-background: var(--primary-error-color); }
  .Notification_type_success {
    --notification-color: var(--secondary-success-color);
    --notification-background: var(--primary-success-color); }
  .Notification_type_info {
    --notification-color: var(--secondary-info-color);
    --notification-background: var(--primary-info-color); }

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateY(-300px); }
  to {
    opacity: 1;
    transform: translateY(0); } }

@keyframes fadeOutLeft {
  from {
    opacity: 1;
    transform: translateY(0);
    max-height: 100px;
    margin-bottom: 5px;
    @media (max-width: 810px) {
      margin-bottom: 20px; } }
  to {
    opacity: 0;
    transform: translateY(-300px);
    max-height: 0;
    padding: 0;
    margin: 0; } }
