@media (max-width: 810px) {
  .NewVersionPopup {
    align-items: flex-end;
    height: 100%; } }

.NewVersionPopup-Heading {
  font-size: 2rem;
  text-decoration: none;
  font-weight: 400;
  margin-top: 0;
  margin-bottom: 1.2rem; }
  @media (max-width: 810px) {
    .NewVersionPopup-Heading {
      margin-bottom: 1.4rem; } }
  @media (min-width: 811px) {
    .NewVersionPopup-Heading {
      display: none; } }

.NewVersionPopup-Buttons {
  margin-top: 1.2rem; }
  @media (max-width: 810px) {
    .NewVersionPopup-Buttons {
      margin-top: 1.4rem; } }
  @media (min-width: 811px) {
    .NewVersionPopup-Buttons {
      display: flex;
      justify-content: space-between; } }

@media (max-width: 810px) {
  .NewVersionPopup-ReloadButton {
    width: 100%; } }

.NewVersionPopup-DismissButton {
  padding: var(--button-padding); }
  @media (max-width: 810px) {
    .NewVersionPopup-DismissButton {
      width: 100%;
      margin-top: 5px; } }

@media (min-width: 811px) {
  .NewVersionPopup-Content {
    max-width: 500px; } }

.NewVersionPopup .Popup-CloseBtn {
  display: none; }

.NewVersionPopup .Popup-Heading {
  margin-bottom: 1.2rem; }
  @media (max-width: 810px) {
    .NewVersionPopup .Popup-Heading {
      margin-bottom: 1.4rem; } }
