.NoMatch {
  opacity: 0;
  animation: appear 100ms 300ms forwards;
  display: flex;
  justify-content: center;
  align-items: center; }
  @media (max-width: 810px) {
    .NoMatch {
      min-height: calc(100vh - var(--header-height) - var(--breadcrumbs-height));
      margin-bottom: 0;
      margin-top: 0; } }
  .NoMatch h1 {
    font-size: 14.4rem;
    font-weight: 400;
    color: var(--primary-base-color);
    line-height: 1; }
    @media (max-width: 810px) {
      .NoMatch h1 {
        font-size: 16.8rem; } }
  .NoMatch-Button {
    margin-top: 2.4rem; }
    @media (max-width: 810px) {
      .NoMatch-Button {
        margin-top: 2.8rem; } }
  .NoMatch-Subtitle {
    font-size: 3.6rem;
    font-weight: 700;
    margin: 0; }
    @media (max-width: 810px) {
      .NoMatch-Subtitle {
        font-size: 4.2rem; } }
  .NoMatch-Wrapper {
    text-align: center; }
