:root {
  --menu-background-color: #fff;
  --menu-desktop-background-color: rgba(122, 122, 122, .44);
  --menu-desktop-height: 60px;
  --menu-item-figure-background: var(--secondary-base-color);
  --menu-item-hover-color: var(--primary-base-color);
  --menu-second-level-color: #adadad; }

.Menu-MenuWrapper {
  padding: 1.2rem; }
  @media (max-width: 810px) {
    .Menu-MenuWrapper {
      padding: 1.4rem; } }
  @media (min-width: 811px) {
    .Menu-MenuWrapper {
      min-height: var(--menu-desktop-height);
      margin: calc(var(--header-nav-height) * -1) auto auto;
      padding: 0; } }
  @media (max-width: 810px) {
    .Menu-MenuWrapper li {
      padding-left: 0; } }
  .Menu-MenuWrapper li::before {
    content: none; }
  .Menu-MenuWrapper li:last-child {
    margin-bottom: 0; }

@media (min-width: 811px) {
  .Menu_HideOnDesktop {
    display: none; } }

.Menu-Overlay {
  background-color: var(--menu-desktop-background-color);
  height: 100vh;
  width: 100%;
  position: absolute;
  z-index: -1;
  top: -3px;
  left: 0;
  opacity: 0;
  transition: opacity 200ms ease-in;
  will-change: transition; }
  .Menu-Overlay_isVisible {
    opacity: 1; }

@media (max-width: 810px) {
  .Menu-MainCategories {
    display: flex;
    flex-direction: column; } }

@media (min-width: 811px) {
  .Menu-MainCategories {
    display: block;
    padding-left: 1.2rem;
    padding-right: 40px;
    z-index: 100;
    max-width: var(--content-wrapper-width);
    width: 100%;
    margin: auto;
    pointer-events: none; } }

.Menu-ItemList {
  display: grid; }
  @media (min-width: 811px) {
    .Menu-ItemList {
      display: flex; } }
  @media (min-width: 811px) {
    .Menu-ItemList_type_main {
      pointer-events: auto;
      width: min-content; } }
  @media (max-width: 810px) {
    .Menu-ItemList_type_main {
      order: 2; } }
  .Menu-ItemList_type_subcategory {
    background: var(--menu-background-color);
    grid-auto-rows: max-content;
    padding: 0 1.2rem; }
    @media (max-width: 810px) {
      .Menu-ItemList_type_subcategory {
        padding: 0 1.4rem; } }
    @media (min-width: 811px) {
      .Menu-ItemList_type_subcategory {
        padding: 0;
        width: 100%;
        display: flex;
        flex-wrap: wrap; } }

.Menu-ItemCaption_type_main {
  font-size: 1.8rem;
  font-weight: bold;
  line-height: 1.2;
  text-align: left; }
  @media (max-width: 810px) {
    .Menu-ItemCaption_type_main {
      font-size: 2.1rem; } }
  @media (min-width: 811px) {
    .Menu-ItemCaption_type_main {
      font-size: 1.4rem;
      font-weight: normal; } }

.Menu-ItemCaption_type_subcategory {
  font-size: 1.2rem;
  font-weight: normal;
  padding: 1.2rem 0; }
  @media (max-width: 810px) {
    .Menu-ItemCaption_type_subcategory {
      font-size: 1.4rem;
      padding: 1.4rem 0; } }
  @media (min-width: 811px) {
    .Menu-ItemCaption_type_subcategory {
      padding-left: 0; } }

.Menu-ItemCaption_isLogo {
  font-size: 1.1rem; }

.Menu-ItemCaption_isBanner {
  --button-background: var(--homepage-button-background);
  --button-border: transparent;
  --button-color: var(--homepage-button-color);
  --button-hover-background: var(--homepage-button-color);
  --button-hover-border: transparent;
  --button-hover-color: var(--homepage-button-background);
  --button-padding: 9px 49px;
  bottom: 20px;
  left: 50%;
  position: absolute;
  transform: translateX(-50%); }

.Menu-ItemFigure {
  align-items: center;
  display: grid; }
  .Menu-ItemFigure_type_main {
    padding-left: 30px;
    text-transform: uppercase; }
    @media (max-width: 810px) {
      .Menu-ItemFigure_type_main {
        background-color: var(--menu-item-figure-background);
        grid-auto-flow: column;
        grid-template-columns: 50% 50%;
        height: 100px; } }
    @media (min-width: 811px) {
      .Menu-ItemFigure_type_main {
        height: var(--menu-desktop-height);
        padding-left: 0;
        z-index: 20; } }
  @media (min-width: 811px) {
    .Menu-ItemFigure_isBanner {
      overflow: hidden;
      width: 200px; } }
  .Menu-ItemFigure_isLogo {
    display: flex; }
    .Menu-ItemFigure_isLogo .Image-Image {
      object-fit: cover; }

@media (max-width: 810px) {
  .Menu-Item {
    margin-top: .6rem;
    padding-left: 0; } }

@media (max-width: 810px) {
  .Menu-Item {
    margin-top: .7rem;
    padding-left: 0; } }

@media (min-width: 811px) {
  .Menu-Item {
    margin-bottom: 0;
    padding: 0 2.4rem; } }

@media (min-width: 811px) and (max-width: 1023px) and (-webkit-min-device-pixel-ratio: 1) and (orientation: portrait) {
  .Menu-Item {
    padding: 0 .5rem; } }

.Menu-Item:first-child {
  margin-top: 0; }
  @media (min-width: 811px) {
    .Menu-Item:first-child {
      padding-left: 0; } }

.Menu-Item button {
  width: 100%; }

.Menu-SubCategoriesWrapper {
  position: absolute;
  top: calc(var(--header-nav-height) + 3px);
  pointer-events: none;
  width: 100%; }
  @media (min-width: 811px) and (max-width: 1024px) and (-webkit-min-device-pixel-ratio: 1) {
    .Menu-SubCategoriesWrapper {
      top: var(--header-nav-height);
      left: 0; } }
  .Menu-SubCategoriesWrapper::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    top: calc(var(--header-nav-height) * -1); }
  .Menu-SubCategoriesWrapper_isVisible {
    pointer-events: all; }

.Menu-SubCategoriesWrapperInner {
  background-color: var(--overlay-background);
  transition: clip-path 200ms ease-in;
  will-change: clip-path;
  clip-path: inset(0 0 100% 0);
  margin: auto;
  overflow-y: auto;
  max-height: calc(100vh - var(--header-nav-height)); }
  .Menu-SubCategoriesWrapperInner_isVisible {
    clip-path: inset(0); }

.Menu-SubCategories, .Menu-Promotion {
  max-width: var(--content-wrapper-width);
  margin: auto; }

.Menu-Promotion {
  order: 3; }

.Menu-SubCategories {
  padding: 40px 55px 60px 1.2rem; }
  @media (max-width: 810px) {
    .Menu-SubCategories {
      padding: 40px 55px 60px 1.4rem; } }

.Menu-SubMenu {
  background-color: var(--overlay-background);
  opacity: 0;
  pointer-events: none;
  touch-action: pan-y;
  transform: translateX(100%);
  transition: transform 200ms ease-in-out; }
  @media (max-width: 810px) {
    .Menu-SubMenu {
      height: 100%;
      left: 0;
      padding-top: var(--header-total-height);
      position: fixed;
      top: 0;
      width: 100%; } }
  @media (min-width: 811px) {
    .Menu-SubMenu {
      background: transparent;
      transform: translateX(0);
      transition-duration: 0ms;
      width: 100%; } }
  .Menu-SubMenu_isVisible {
    pointer-events: all;
    z-index: 10;
    opacity: 1;
    transform: translateX(0); }
    @media (max-width: 810px) {
      .Menu-SubMenu_isVisible {
        height: 100%;
        overflow-y: scroll; } }
    @media (min-width: 811px) {
      .Menu-SubMenu_isVisible .Menu-Link {
        padding: 0; } }
  @media (min-width: 811px) {
    .Menu-SubMenu .Menu-Link {
      margin-left: 0;
      margin-right: 0; } }

.Menu-SubItemWrapper {
  flex-basis: 200px; }
  @media (min-width: 811px) {
    .Menu-SubItemWrapper {
      margin-right: 4.8rem;
      margin-bottom: 2.4rem; } }
  .Menu-SubItemWrapper_isBanner {
    flex-grow: 1;
    flex-basis: 400px; }
  .Menu-SubItemWrapper > .Menu-Link {
    display: block; }
    @media (min-width: 811px) {
      .Menu-SubItemWrapper > .Menu-Link {
        color: var(--menu-second-level-color);
        font-size: 1.4rem;
        text-transform: uppercase; } }

.Menu-SubLevelDesktop .Menu-ItemList {
  display: block;
  padding: 0; }
  .Menu-SubLevelDesktop .Menu-ItemList_isBanner {
    display: grid;
    grid-column-gap: 10px;
    grid-template-columns: repeat(auto-fit, 200px); }

.Menu-SubLevelDesktop .Menu-Link {
  margin-top: 20px; }

@media (min-width: 811px) {
  .Menu-Image_isBanner {
    padding-bottom: 100%; } }

.Menu-Image_isBanner img {
  object-fit: cover; }

.Menu-Image_isLogo {
  height: 44px;
  margin-right: 20px;
  width: 44px; }

.Menu-Image_type_main {
  grid-column: 2;
  height: 100%;
  justify-self: end;
  padding-bottom: 0; }

.Menu-Image_type_subcategory {
  border-radius: 50%;
  height: 3.84rem;
  padding-bottom: 0;
  width: 3.84rem; }
  @media (max-width: 810px) {
    .Menu-Image_type_subcategory {
      height: 4.48rem;
      width: 4.48rem; } }

.Menu-Link {
  color: inherit;
  white-space: nowrap;
  padding-left: 0; }
  @media (max-width: 810px) {
    .Menu-Link {
      padding: 1.2rem 0; } }
  @media (max-width: 810px) {
    .Menu-Link {
      padding: 1.4rem 0; } }
  @media (min-width: 811px) {
    .Menu-Link {
      display: block;
      margin-right: 0; } }
  .Menu-Link:hover, .Menu-Link_isHovered, .Menu-Link:focus {
    color: var(--menu-item-hover-color);
    text-decoration: none; }

.Menu-Link + .Menu-Link {
  margin-left: 0; }

.Menu .Image {
  background: none; }
