:root {
  --navigation-tabs-height: calc(50px + env(safe-area-inset-bottom));
  --navigation-tabs-background: var(--secondary-light-color);
  --navigation-tabs-color: var(--secondary-dark-color); }

.NavigationTabs {
  --header-background: var(--navigation-tabs-background);
  --header-color: var(--navigation-tabs-color);
  --header-icon-stroke-width: 2px;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  background-color: var(--header-background);
  padding-bottom: env(safe-area-inset-bottom);
  transform: translateY(0);
  transition: transform 200ms cubic-bezier(0.47, 0, 0.745, 0.715); }
  .hideOnScroll .NavigationTabs {
    transform: translateY(100%); }
  @media (max-width: 810px) {
    .NavigationTabs {
      border-top: 1px solid var(--primary-divider-color); } }
  @media (min-width: 811px) {
    .NavigationTabs {
      display: none; } }
  .NavigationTabs_isHidden {
    display: none; }
    .hiddenNavigationTabs {
      --navigation-tabs-height: env(safe-area-inset-bottom); }
  .NavigationTabs-Nav {
    height: var(--header-nav-height);
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 0 1.2rem;
    z-index: 20;
    max-width: var(--content-wrapper-width);
    margin: auto; }
    @media (max-width: 810px) {
      .NavigationTabs-Nav {
        padding: 0 1.4rem; } }
  .NavigationTabs-Button {
    flex-grow: 1;
    height: 100%;
    display: inherit;
    justify-content: center;
    align-items: center; }
  .NavigationTabs-Icon {
    font-weight: bold; }
    .NavigationTabs-Icon_isActive {
      --header-color: var(--primary-base-color); }
