:root {
  --popup-background: #0003;
  --popup-content-background: #fff;
  --popup-min-width: 100%;
  --popup-content-padding: 1.2rem; }
  @media (max-width: 810px) {
    :root {
      --popup-content-padding: 1.4rem; } }
  @media (min-width: 811px) {
    :root {
      --popup-min-width: 490px;
      --popup-content-padding: 2.4rem; } }

.Popup {
  opacity: 0;
  pointer-events: none;
  z-index: 200;
  position: fixed;
  background-color: var(--popup-background);
  left: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center; }
  @media (max-width: 810px) {
    .Popup {
      align-items: stretch; } }
  .Popup_isVisible {
    pointer-events: all;
    opacity: 1;
    width: 100vw;
    height: 100vh; }
    @media (max-width: 810px) {
      .Popup_isVisible {
        height: calc(100% - var(--header-total-height)); } }
  .Popup-Heading {
    font-size: 2rem;
    text-decoration: none;
    font-weight: 400;
    margin-top: 0; }
  @media (max-width: 810px) {
    .Popup-Header {
      display: none; } }
  .Popup-Content {
    background-color: var(--popup-content-background);
    border-radius: 5px;
    padding: var(--popup-content-padding);
    min-width: var(--popup-min-width);
    max-width: calc(var(--content-wrapper-width) * .8);
    max-height: 80%;
    overflow-y: scroll; }
    @media (max-width: 810px) {
      .Popup-Content {
        border-radius: 0;
        max-height: 100%; } }
  .Popup .Popup-CloseBtn {
    height: 40px;
    width: 40px;
    position: absolute;
    top: -4px;
    right: -1rem;
    z-index: 5; }
    .Popup .Popup-CloseBtn::before, .Popup .Popup-CloseBtn::after {
      content: '';
      position: absolute;
      width: 1px;
      height: 20px;
      transform-origin: 50% 50%;
      top: 7px;
      transform: rotate(-45deg);
      background-color: var(--header-color); }
    .Popup .Popup-CloseBtn::after {
      transform: rotate(45deg); }
    @media (max-width: 810px) {
      .Popup .Popup-CloseBtn {
        top: 25px; } }
  @media (max-width: 810px) {
    .Popup .Loader {
      position: fixed; } }

.scrollDisabled {
  position: fixed;
  margin-top: 0;
  width: 100%;
  overflow-y: scroll; }
