.CurrencySwitcher .FieldSelect-Select {
  border: none;
  border-bottom: 1px solid var(--input-border-color); }

@media (min-width: 811px) {
  .CurrencySwitcher {
    display: inline-block; }
    .CurrencySwitcher .Field {
      margin: .5rem; }
    .CurrencySwitcher .FieldSelect::after {
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      border-left: 3px solid transparent;
      border-right: 3px solid transparent;
      border-bottom: 3px solid black;
      transition: transform 200ms ease-in-out;
      transform: rotate(180deg);
      will-change: transform; }
    .CurrencySwitcher .FieldSelect[aria-expanded='true']::after {
      transform: rotate(0); }
    .CurrencySwitcher .FieldSelect-Select {
      border: none;
      font-size: 1rem;
      padding: 0;
      min-height: auto; }
    .CurrencySwitcher .FieldSelect-Options {
      overflow-y: auto;
      text-align: center;
      margin-top: 1rem; }
    .CurrencySwitcher .FieldSelect-Option {
      padding: 0;
      line-height: 2.8rem;
      font-size: 1.2rem; } }
