.TextPlaceholder {
  pointer-events: none; }
  .TextPlaceholder::after {
    font-family: 'Courier New', Courier, monospace;
    color: transparent;
    background-image: var(--placeholder-image);
    background-size: var(--placeholder-size);
    animation: var(--placeholder-animation); }
  .TextPlaceholder_loaded {
    pointer-events: all; }
    .TextPlaceholder_loaded::after {
      opacity: 0; }
  .TextPlaceholder_length_block::after {
    content: 'aa'; }
  .TextPlaceholder_length_short::after {
    content: 'aaaaaaaaaaaaa'; }
    @media (max-width: 810px) {
      .TextPlaceholder_length_short::after {
        content: 'aaaaaaaa'; } }
  .TextPlaceholder_length_medium::after {
    content: 'aaaaaaaaaaaaaaaaaaaaaaaaaa';
    word-wrap: break; }
    @media (max-width: 810px) {
      .TextPlaceholder_length_medium::after {
        content: 'aaaaaaaaaaaaaaaa'; } }
  .TextPlaceholder_length_long::after {
    content: 'aaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaa'; }
    @media (max-width: 810px) {
      .TextPlaceholder_length_long::after {
        content: 'aaaaaaaaaaaaaaaaaaaaaaaa'; } }
  .TextPlaceholder_length_paragraph::after {
    content: '';
    word-break: break-all; }
    @media (max-width: 810px) {
      .TextPlaceholder_length_paragraph::after {
        content: 'aaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaa'; } }
