.NotificationList {
  position: fixed;
  z-index: 300;
  top: calc(var(--header-height) + var(--breadcrumbs-height) + 20px);
  right: 30px;
  max-width: 550px;
  list-style: none; }
  @media (min-width: 811px) {
    .NotificationList {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-end; } }
  @media (max-width: 810px) {
    .NotificationList {
      overflow: hidden;
      max-width: 100vw;
      position: sticky;
      width: 100%;
      top: var(--header-total-height);
      right: 0; } }
