:root {
  --store-switcher-list-background: #fff;
  --store-switcher-title-color: #414141; }

@media (min-width: 811px) {
  .StoreSwitcher {
    display: inline-block; } }

@media (max-width: 810px) {
  .StoreSwitcher {
    --input-padding: 1.2rem;
    border-bottom: 1px solid var(--primary-divider-color);
    order: 1;
    margin-bottom: 1.2rem; } }

@media (max-width: 810px) {
  .StoreSwitcher {
    --input-padding: 1.4rem;
    margin-bottom: 1.4rem; } }

@media (max-width: 810px) {
  .StoreSwitcher select,
  .StoreSwitcher select:hover,
  .StoreSwitcher select:focus {
    border: 0; } }

@media (max-width: 810px) {
  .StoreSwitcher .Field {
    margin-top: 0; } }

.StoreSwitcher-Title {
  color: var(--store-switcher-title-color);
  cursor: pointer;
  font-size: 1.2rem; }
  @media (max-width: 810px) {
    .StoreSwitcher-Title {
      font-size: 1.4rem; } }
  .StoreSwitcher-Title::after {
    content: '';
    position: absolute;
    right: -12px;
    top: 8px;
    width: 0;
    height: 0;
    border-left: 3px solid transparent;
    border-right: 3px solid transparent;
    border-bottom: 3px solid black;
    transition: transform 200ms ease-in-out;
    transform: rotate(180deg);
    will-change: transform; }
  .StoreSwitcher-Title_isOpen::after {
    transform: rotate(0); }

.StoreSwitcher-StoreList {
  background-color: var(--store-switcher-list-background);
  border: 1px solid var(--input-border-color);
  display: none;
  right: -20px;
  position: absolute;
  top: 25px; }
  .StoreSwitcher-StoreList_isOpen {
    display: block; }
