:root {
  --select-option-background: #fff;
  --select-option-hover-background: #f0f0f0; }

.FieldSelect {
  cursor: pointer; }
  .FieldSelect::after {
    content: '';
    display: block;
    width: 6px;
    height: 6px;
    transform: translateY(-2px) rotate(45deg);
    pointer-events: none;
    position: absolute;
    right: 20px;
    bottom: 0;
    top: 0;
    margin: auto;
    border-style: solid;
    border-color: #000;
    border-width: 0 1px 1px 0; }
  @media (min-width: 811px) {
    .FieldSelect_isExpanded::after {
      bottom: 9px;
      transform: translateY(-2px) rotate(-135deg); } }
  .FieldSelect-Select {
    width: 100%;
    min-width: 100%;
    padding-right: 40px; }
    @media (min-width: 811px) {
      .FieldSelect-Select {
        pointer-events: none; } }
    .FieldSelect-Select_isExpanded {
      border-color: var(--input-color); }
  .FieldSelect-Options {
    position: absolute;
    width: 100%;
    top: 100%;
    left: 0;
    z-index: 40;
    border-width: 0 1px 1px;
    border-style: solid;
    border-color: transparent;
    overflow-y: scroll;
    transition: 200ms max-height;
    will-change: max-height;
    max-height: 0;
    -webkit-overflow-scrolling: touch; }
    @media (min-width: 811px) {
      .FieldSelect-Options_isExpanded {
        max-height: 200px;
        border-color: var(--input-border-color); } }
  .FieldSelect-Option {
    padding: 0;
    margin: 0;
    z-index: 1;
    transition: 100ms max-height ease-in;
    will-change: max-height;
    max-height: 36px;
    line-height: 3.6rem;
    padding-left: 1.8rem;
    background: var(--select-option-background); }
    @media (max-width: 810px) {
      .FieldSelect-Option {
        line-height: 4.2rem;
        padding-left: 2.1rem; } }
    .FieldSelect-Option::before {
      content: none; }
    .FieldSelect-Option:last-of-type {
      margin: 0; }
    @media (min-width: 811px) {
      .FieldSelect-Option:hover, .FieldSelect-Option:focus {
        background: var(--select-option-hover-background); } }
