:root {
  --cart-overlay-totals-background: var(--secondary-base-color);
  --cart-overlay-divider-background: #d8d8d8;
  --cart-overlay-promo-background: #fff;
  --cart-overlay-width: 400px; }

@media (min-width: 811px) {
  .CartOverlay {
    position: absolute;
    top: calc(var(--header-nav-height) - 7px);
    right: 22px;
    padding: 0;
    width: var(--cart-overlay-width); } }

@media (min-width: 811px) {
  .CartOverlay_isVisible {
    border: 1px solid var(--overlay-desktop-border-color); } }

@media (min-width: 811px) {
  .CartOverlay::before {
    content: '';
    position: absolute;
    top: -9px;
    right: 9px;
    height: 15px;
    width: 15px;
    transform: rotate(45deg);
    background-color: var(--overlay-desktop-border-color);
    border-top: 1px solid var(--overlay-desktop-border-color);
    border-left: 1px solid var(--overlay-desktop-border-color); } }

.CartOverlay-Empty {
  padding: 1.2rem; }
  @media (max-width: 810px) {
    .CartOverlay-Empty {
      padding: 1.4rem; } }

.CartOverlay-Actions {
  display: flex;
  padding: 1.2rem; }
  @media (max-width: 810px) {
    .CartOverlay-Actions {
      padding: 1.4rem; } }

.CartOverlay-OutOfStockProductsWarning {
  padding: 1rem;
  display: flex;
  justify-content: center;
  background-color: var(--primary-error-color, red);
  font-size: 14px;
  font-weight: bold; }

.CartOverlay-CheckoutButton {
  margin-left: 1.2rem; }
  .CartOverlay-CheckoutButton span {
    display: inline-block;
    margin-right: 9px; }
    .CartOverlay-CheckoutButton span::before, .CartOverlay-CheckoutButton span::after {
      content: ''; }
    .CartOverlay-CheckoutButton span::after {
      display: block;
      position: relative;
      top: 1px;
      width: 10px;
      height: 9px;
      background: currentColor;
      border-radius: 1px;
      box-shadow: inset 0 0 0 -2px var(--button-background); }
    .CartOverlay-CheckoutButton span::before {
      position: absolute;
      left: 0;
      right: 0;
      margin: auto;
      top: -4px;
      border-radius: 50%;
      border: 1px solid currentColor;
      width: 6px;
      height: 6px; }
  @media (max-width: 810px) {
    .CartOverlay-CheckoutButton {
      margin-left: 1.4rem;
      width: 100%;
      display: block;
      text-align: center; } }
  @media (min-width: 811px) {
    .CartOverlay-CheckoutButton {
      flex-grow: 1;
      text-align: center; } }

@media (max-width: 810px) {
  .CartOverlay-CartButton {
    display: none; } }

.CartOverlay-Promo, .CartOverlay-Total {
  min-height: 4.8rem; }
  @media (max-width: 810px) {
    .CartOverlay-Promo, .CartOverlay-Total {
      min-height: 5.6rem; } }
  @media (min-width: 811px) {
    .CartOverlay-Promo, .CartOverlay-Total {
      min-height: 3.6rem; } }

.CartOverlay-Promo {
  padding: 1.2rem;
  text-align: center;
  background: var(--cart-overlay-promo-background);
  margin-bottom: 0; }
  @media (max-width: 810px) {
    .CartOverlay-Promo {
      padding: 1.4rem;
      border-bottom: 1px solid var(--cart-overlay-divider-background);
      border-top: 1px solid var(--expandable-content-color); } }
  @media (min-width: 811px) {
    .CartOverlay-Promo {
      padding: .84rem;
      background: var(--primary-light-color); } }
  .CartOverlay-Promo strong {
    margin: 0 5px; }

.CartOverlay-Items {
  max-height: 40vh;
  overflow-y: scroll; }

.CartOverlay-Total, .CartOverlay-Discount, .CartOverlay-Tax {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.2rem 1.2rem .6rem;
  font-size: 1.8rem;
  background: var(--cart-overlay-totals-background); }
  @media (max-width: 810px) {
    .CartOverlay-Total, .CartOverlay-Discount, .CartOverlay-Tax {
      padding: 1.4rem 1.4rem .7rem; } }
  @media (min-width: 811px) {
    .CartOverlay-Total, .CartOverlay-Discount, .CartOverlay-Tax {
      font-size: 1.6rem; } }

.CartOverlay-Total {
  font-weight: bold;
  padding: .6rem 1.2rem 1.2rem; }
  @media (max-width: 810px) {
    .CartOverlay-Total {
      padding: .7rem 1.4rem 1.4rem; } }
  @media (min-width: 811px) {
    .CartOverlay-Total {
      border-bottom: 1px solid var(--cart-item-divider-color); } }

.CartOverlay-DiscountCoupon::after {
  content: ':';
  font-weight: normal; }
