.SomethingWentWrong {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: var(--secondary-base-color);
  min-height: 100vh;
  margin-top: 0;
  padding: 1.2rem;
  margin-bottom: 0; }
  @media (max-width: 810px) {
    .SomethingWentWrong {
      padding: 1.4rem; } }
  .SomethingWentWrong-Debug {
    text-align: left;
    white-space: pre-line;
    margin-top: 4.8rem;
    font-family: 'Droid Sans Mono', 'monospace', monospace, 'Droid Sans Fallback';
    background-color: #0a0a0a;
    color: #fff;
    padding: 1.2rem;
    overflow: scroll;
    max-width: calc(100vw - 2.4rem); }
    @media (max-width: 810px) {
      .SomethingWentWrong-Debug {
        margin-top: 5.6rem;
        padding: 1.4rem;
