:root {
  --newsletter-subscription-input-background: var(--input-background-color);
  --newsletter-subscription-input-color: #0a0a0a;
  --newsletter-subscription-placeholder-color: var(--secondary-dark-color);
  --newsletter-subscription-button-background: var(--link-color); }

.NewsletterSubscription {
  min-width: 230px;
  padding: 0 1rem; }
  @media (min-width: 811px) {
    .NewsletterSubscription {
      padding: 0; } }
  .NewsletterSubscription .FieldForm-Fieldset {
    display: flex;
    flex-wrap: nowrap; }
    .NewsletterSubscription .FieldForm-Fieldset_isLoading {
      opacity: .7;
      pointer-events: none; }
    .NewsletterSubscription .FieldForm-Fieldset .Field {
      width: 100%;
      margin-top: 0; }
      .NewsletterSubscription .FieldForm-Fieldset .Field-Message {
        position: absolute; }
      .NewsletterSubscription .FieldForm-Fieldset .Field input {
        background-color: var(--newsletter-subscription-input-background);
        color: var(--newsletter-subscription-input-color);
        border: 1px solid transparent;
        padding: 14px 12px; }
        .NewsletterSubscription .FieldForm-Fieldset .Field input::placeholder {
          color: var(--newsletter-subscription-placeholder-color); }
      .NewsletterSubscription .FieldForm-Fieldset .Field_hasError input {
        border: 1px solid var(--primary-error-color);
        border-right: 0; }
  .NewsletterSubscription .FieldForm-Fields {
    margin-bottom: 0; }
  .NewsletterSubscription .FieldForm-Button {
    width: 44px;
    background-color: var(--newsletter-subscription-button-background);
    border-color: var(--newsletter-subscription-button-background); }
    .NewsletterSubscription .FieldForm-Button:focus, .NewsletterSubscription .FieldForm-Button:hover {
      border-color: var(--newsletter-subscription-button-background);
      background-color: var(--newsletter-subscription-button-background); }
    .NewsletterSubscription .FieldForm-Button::before, .NewsletterSubscription .FieldForm-Button::after {
      content: '';
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      box-sizing: border-box;
      border: 1px solid #fff; }
    .NewsletterSubscription .FieldForm-Button::before {
      width: 18px;
      height: 14px;
      transform: translate(-50%, -50%); }
    .NewsletterSubscription .FieldForm-Button::after {
      width: 12px;
      height: 12px;
      transform: rotate(45deg) translate(calc(-50% - 4px), calc(-50% - 4px));
      transform-origin: top left;
      border-top-color: transparent;
      border-left-color: transparent; }
  .NewsletterSubscription .Form_isInvalid {
    margin-bottom: 1.8rem; }
