:root {
  --search-bar-max-width: 450px;
  --search-bar-width: 270px;
  --search-clear-background: var(--secondary-base-color);
  --search-field-background: var(--secondary-base-color);
  --search-field-border-color: var(--primary-divider-color);
  --search-field-color: var(--secondary-base-color);
  --search-field-visible-background: #fff;
  --search-input-desktop-border-color: #000; }

@media (max-width: 810px) {
  .SearchField {
    flex-grow: 1;
    opacity: 0;
    pointer-events: none; } }

@media (min-width: 811px) {
  .SearchField {
    text-align: right; } }

.SearchField_isVisible {
  opacity: 1;
  pointer-events: all; }

.SearchField_isActive {
  --search-bar-width: 27vw;
  --search-field-border-color: var(--primary-light-color); }

@media (min-width: 811px) {
  .SearchField-Wrapper {
    margin-right: 1.2rem; } }

.SearchField-Input {
  transition-property: opacity, background-color;
  transition-duration: 200ms;
  background-color: var(--search-field-background);
  border-color: transparent;
  border-radius: 15px;
  border-width: 1px;
  font-size: 1.6rem;
  max-width: 0;
  min-width: 0;
  opacity: 1;
  width: 100%;
  padding-bottom: 5px;
  padding-left: 10px;
  padding-top: 4px; }
  @media (max-width: 810px) {
    .SearchField-Input {
      max-width: 100%; } }
  @media (min-width: 811px) {
    .SearchField-Input {
      background-color: transparent;
      border: 0;
      border-bottom: 1px solid var(--search-input-desktop-border-color);
      border-radius: 0;
      font-size: 1.4rem;
      margin: 4.8rem 3.6rem 0 0;
      max-width: 100%;
      padding-left: 3.6rem;
      width: calc(100% - 7.2rem);
      padding-bottom: 7px; } }
  .SearchField-Input:focus, .SearchField-Input:active {
    --input-color: var(--search-field-color); }
    @media (min-width: 811px) {
      .SearchField-Input:focus, .SearchField-Input:active {
        border: 0;
        border-bottom: 1px solid var(--search-input-desktop-border-color); } }

.SearchField-Placeholder {
  font-size: 1.6rem;
  height: min-content;
  left: calc(50% - 5px);
  padding-left: 20px;
  pointer-events: none;
  position: absolute;
  top: 5px;
  transform: translate3d(-50%, 0, 0);
  visibility: hidden;
  width: min-content; }
  .SearchField-Placeholder::before {
    content: '';
    position: absolute;
    width: calc(10px - var(--header-icon-stroke-width) * 2);
    height: calc(10px - var(--header-icon-stroke-width) * 2);
    border: var(--header-icon-stroke-width) solid var(--header-color);
    border-radius: 50%;
    box-shadow: 4px 4px 0 -4px var(--header-color), 5px 5px 0 -4px var(--header-color), 6px 6px 0 -4px var(--header-color), 7px 7px 0 -4px var(--header-color); }
  .SearchField-Placeholder::before {
    bottom: 0;
    left: 0;
    margin: auto;
    top: 0;
    transform: translateY(-2px); }
  .SearchField-Placeholder_isActive {
    left: 0;
    opacity: .7;
    padding-left: 0;
    transform: translate3d(10px, 0, 0); }
    .SearchField-Placeholder_isActive::before {
      opacity: 0; }
  .SearchField-Placeholder_isPlaceholderVisible {
    visibility: visible; }

.SearchField-SearchIcon, .SearchField-CloseIcon {
  cursor: pointer; }
  @media (min-width: 811px) {
    .SearchField-SearchIcon, .SearchField-CloseIcon {
      height: 24px;
      width: 16px; } }

.SearchField-SearchIcon::before {
  content: '';
  position: absolute;
  width: calc(10px - var(--header-icon-stroke-width) * 2);
  height: calc(10px - var(--header-icon-stroke-width) * 2);
  border: var(--header-icon-stroke-width) solid var(--header-color);
  border-radius: 50%;
  box-shadow: 4px 4px 0 -4px var(--header-color), 5px 5px 0 -4px var(--header-color), 6px 6px 0 -4px var(--header-color), 7px 7px 0 -4px var(--header-color); }

.SearchField-SearchIcon::before {
  box-shadow: 6px 6px 0 -7px var(--header-color), 7px 7px 0 -7px var(--header-color), 8px 8px 0 -7px var(--header-color), 9px 9px 0 -7px var(--header-color), 10px 10px 0 -7px var(--header-color), 11px 11px 0 -7px var(--header-color);
  height: calc(16px - var(--header-icon-stroke-width) * 2);
  left: 0;
  top: 2px;
  width: calc(16px - var(--header-icon-stroke-width) * 2); }

.SearchField-CloseIcon::before, .SearchField-CloseIcon::after {
  content: '';
  position: absolute;
  width: 1px;
  height: 20px;
  transform-origin: 50% 50%;
  top: 7px;
  transform: rotate(-45deg);
  background-color: var(--header-color); }

.SearchField-CloseIcon::after {
  transform: rotate(45deg); }

.SearchField-CloseIcon::before, .SearchField-CloseIcon::after {
  height: 22px;
  left: 8px;
  top: 2px;
  width: 2px; }

.SearchField-SearchWrapper {
  background: var(--search-field-visible-background);
  height: 100%;
  max-width: 360px;
  position: fixed;
  right: 0;
  top: calc(var(--header-total-height) + 3px);
  transform: translateX(100%);
  transition: transform 300ms ease-in;
  width: 100%;
  will-change: transform;
  border-left: 1px solid var(--primary-divider-color); }
  .SearchField-SearchWrapper_isVisible {
    transform: translateX(0); }

.SearchField-SearchInnerWrapper .SearchField-SearchIcon {
  left: 4.2rem;
  position: absolute;
  top: 4.8rem; }
  @media (max-width: 810px) {
    .SearchField-SearchInnerWrapper .SearchField-SearchIcon {
      left: 4.9rem;
      top: 5.6rem; } }
