:root {
  --image-default-background: var(--secondary-base-color);
  --image-content-color: #0a0a0a; }

.Image {
  display: inline-block;
  overflow: hidden;
  height: 0;
  width: 100%;
  background: var(--image-default-background); }
  .Image_ratio_16x9 {
    padding-bottom: 56.25%; }
  .Image_ratio_4x3 {
    padding-bottom: 75%; }
  .Image_ratio_square {
    padding-bottom: 100%; }
  .Image_imageStatus_0, .Image_isPlaceholder {
    background-image: var(--placeholder-image);
    background-size: var(--placeholder-size);
    animation: var(--placeholder-animation); }
  .Image_hasSrc, .Image_imageStatus_1 {
    background: none; }
  .Image-Content {
    position: absolute;
    height: max-content;
    width: 100%;
    text-align: center;
    margin: auto;
    bottom: 0;
    top: 0;
    left: 0;
    color: var(--image-content-color);
    font-size: 1.4rem; }
    .Image-Content_isOffline {
      width: 100%;
      height: 100%;
      background: var(--image-default-background); }
  .Image-Image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-position: 50% 50%;
    object-fit: contain; }
    .Image-Image::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: var(--image-default-background); }
