:root {
  --cookiepopup-bg: #fff;
  --cookiepopup-border: var(--secondary-base-color); }

.CookiePopup {
  display: flex;
  width: 100vw;
  position: fixed;
  left: 0;
  bottom: 0;
  align-items: center;
  justify-content: space-between;
  padding: 1.2rem 0 calc(env(safe-area-inset-bottom) + 1.2rem);
  background-color: var(--cookiepopup-bg);
  border-top: 1px solid var(--cookiepopup-border);
  z-index: 300; }
  @media (max-width: 810px) {
    .CookiePopup {
      padding: 1.4rem 0 calc(env(safe-area-inset-bottom) + 1.4rem); } }
  @media (min-width: 811px) {
    .CookiePopup-ContentWrapper {
      display: flex;
      justify-content: space-between;
      align-items: center; } }
  .CookiePopup-Wrapper {
    width: 100%; }
  .CookiePopup-Content {
    font-size: 1.5rem;
    max-width: 500px; }
    @media (max-width: 810px) {
      .CookiePopup-Content {
        padding-bottom: 1.4rem; } }
  .CookiePopup-Link {
    display: inline;
    margin-left: 5px; }
  @media (max-width: 810px) {
    .CookiePopup-CTA button {
      width: 100%; } }
  @media (min-width: 811px) {
    .CookiePopup-CTA {
      margin-left: 2.4rem; } }
