:root {
  --footer-height: calc(var(--footer-content-height) + var(--footer-copyright-height));
  --footer-content-height: 320px;
  --footer-copyright-height: 50px; }

@media (max-width: 810px) {
  .Footer {
    margin-bottom: var(--navigation-tabs-height); } }

.Footer-CopyrightContentWrapper {
  background-color: var(--secondary-dark-color); }

.Footer-CopyrightContent {
  padding: 10px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: var(--footer-copyright-height); }
  @media (min-width: 811px) {
    .Footer-CopyrightContent {
      padding: 15px 0; } }

.Footer-Copyright {
  font-size: 1.5rem;
  text-align: center; }
  .Footer-Copyright a {
    color: inherit; }

.Footer-Content {
  min-height: var(--footer-content-height);
  background-color: var(--secondary-base-color); }

.Footer-Columns {
  display: flex;
  height: 100%;
  padding: 25px 40px;
  justify-content: space-between; }
  @media (max-width: 810px) {
    .Footer-Columns {
      flex-direction: column;
      align-items: center;
      text-align: center; } }
  @media (min-width: 811px) {
    .Footer-Columns {
      padding: 60px 70px; } }
  @media (min-width: 811px) and (max-width: 1024px) and (-webkit-min-device-pixel-ratio: 1) {
    .Footer-Columns {
      padding-left: 30px;
      padding-right: 30px; } }

@media (max-width: 810px) {
  .Footer-Column:not(:last-of-type) {
    margin-bottom: 30px; } }

@media (min-width: 811px) {
  .Footer-Column:not(:last-of-type) {
    padding-right: 20px;
    max-width: calc(100% / 3); } }

.Footer-ColumnTitle {
  text-transform: uppercase;
  margin: 0 0 15px;
  font-size: 2rem; }
  @media (min-width: 811px) {
    .Footer-ColumnTitle {
      margin: 0 0 20px; } }

.Footer-ColumnContent {
  display: flex;
  flex-direction: column; }
  .Footer-ColumnContent_direction_horizontal {
    flex-direction: row; }
    @media (max-width: 810px) {
      .Footer-ColumnContent_direction_horizontal {
        justify-content: center; } }
  .Footer-ColumnContent a + a {
    margin-left: 0; }

.Footer-ColumnItem {
  color: inherit;
  margin-bottom: 12px;
  font-size: 1.6rem; }
  .Footer-ColumnItem:last-of-type {
    margin: 0; }
  .Footer-ColumnItem_type_image {
    width: 25px; }
    .Footer-ColumnItem_type_image:not(:last-of-type) {
      margin-right: 20px; }

.Footer-CmsBlockWrapper .CmsBlock-Wrapper {
  flex: 1; }

.Footer-CmsBlockWrapper .ContentWrapper .Footer-Columns {
  padding: 0; }

@media (min-width: 811px) {
  .Footer-CmsBlockWrapper .Footer-Column_isNewsletter {
    padding-left: 100px; } }
